/*
  elm-hot creates an additional div wrapper around the app to make HMR possible.
  This could break styling in development mode if you are using Elm UI.

  More context in the issue:
    https://github.com/halfzebra/create-elm-app/issues/320
*/
[data-elm-hot="true"] {
    height: inherit;
}

body {
    font-family: 'Roboto Condensed', 'Source Sans Pro', 'Trebuchet MS', 'Lucida Grande', 'Bitstream Vera Sans', 'Helvetica Neue', sans-serif;
    margin: 0;
    text-align: center;
    color: #293c4b;
    background-color: #eeeeee;
}

div.header {
    height: 8em;
    margin: 0;
}

div.header > h1 {
    font-size: 6em;
    display: block;
    float: left;    
    margin:0;
    margin-left: 0.5em;
    -webkit-animation-name: fade-opacity;
            animation-name: fade-opacity;
    -webkit-animation-duration: 2.25s;
            animation-duration: 2.25s;
}

@-webkit-keyframes fade-opacity {
    from { opacity: 0; }
    to { opacity: 1; }
}

@keyframes fade-opacity {
    from { opacity: 0; }
    to { opacity: 1; }
}

div.header > img.logo {
    height: 100%;
    min-height: 100%;
    float:left;
    padding: 0;
    margin: 0;
    clear: both;
    -webkit-animation-name: rotate-cw-logo;
            animation-name: rotate-cw-logo;
    -webkit-animation-duration: 0.25s;
            animation-duration: 0.25s;
}

div.header > img.clicked {
    height: 100%;
    min-height: 100%;
    float:left;
    clear: both;
    padding: 0;
    top: 0;
    left:0;
    -webkit-animation-name: rotate-cc-logo;
            animation-name: rotate-cc-logo;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

@-webkit-keyframes rotate-cc-logo {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(90deg); transform: rotate(90deg); }
}

@keyframes rotate-cc-logo {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(90deg); transform: rotate(90deg); }
}

@-webkit-keyframes rotate-cw-logo {
    from { -webkit-transform: rotate(90deg); transform: rotate(90deg); }
    to { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
}

@keyframes rotate-cw-logo {
    from { -webkit-transform: rotate(90deg); transform: rotate(90deg); }
    to { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
}

div.menu {
    display: block;
    float: left;
    height: 8em;
    margin: 0.25em;
    font-size: 1.75em;
    /* font-size: 1.25em; */
    /* background: #0000ff; */
}

div.menu > ul {
    list-style: none;
    margin: 0;
    float: left;
    padding: 0;
    margin: 0.5em;
}

a {
    color: inherit;
    text-decoration: none;
}

div.menu a:hover {
    text-decoration: underline;
}

div.menu li {
    text-align: left;
}

div.menu.active {
    -webkit-animation-name: show-menu;
            animation-name: show-menu;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

div.menu.hidden {
    -webkit-animation-name: hide-menu;
            animation-name: hide-menu;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

@-webkit-keyframes show-menu {
    from { -webkit-transform: translateX(-100%); transform: translateX(-100%); }
    to { -webkit-transform: translateX(0%); transform: translateX(0%); }
}

@keyframes show-menu {
    from { -webkit-transform: translateX(-100%); transform: translateX(-100%); }
    to { -webkit-transform: translateX(0%); transform: translateX(0%); }
}

@-webkit-keyframes hide-menu {
    from { -webkit-transform: translateX(0%); transform: translateX(0%); }
    to { -webkit-transform: translateX(-200%); transform: translateX(-200%); }
}

@keyframes hide-menu {
    from { -webkit-transform: translateX(0%); transform: translateX(0%); }
    to { -webkit-transform: translateX(-200%); transform: translateX(-200%); }
}

div.content {
    display: block;
    float: left;
    margin: 12px;
    /* background: #ff0000; */
}
